import React from "react"
import $ from "jquery"
import { graphql, StaticQuery } from "gatsby"
import LazyLoad from "react-lazyload"

const SRC = "https://www.tesla.com/sites/default/files/images/software_update.jpg"


class BannerLeft extends React.Component {
  componentDidMount() {
    const $banner = $(".ads-left"),
      $window = $(window)
    const $wiBanner = $banner.outerWidth() * 2
    zindex($("#wrapper").outerWidth())
    $window.on("resize", function() {
      $banner
        .stop()
        .animate({ left: $(".container").offset().left - 175 }, 1000)
      zindex($("#wrapper").outerWidth())
    })

    // $banner.stop().animate( { left: $('.container').offset().left - 175 }, 1000 );
    function zindex(maxWidth) {
      if ($window.width() <= maxWidth + $wiBanner) {
        $banner.addClass("zindex")
      } else {
        $banner.removeClass("zindex")
      }
    }
  }

  render() {
    const { data } = this.props
    const { allWordpressWpQuangCao = {} } = data
    const { edges = [] } = allWordpressWpQuangCao
    const { node = {} } = edges[0]
    const { metadata = {} } = node
    const { image_code = "" } = metadata
    return (
      <LazyLoad once>
        <div className="ads-left" dangerouslySetInnerHTML={{ __html: image_code.replace("amp-img", "img") }}>
        </div>
      </LazyLoad>
    )
  }
}


export default props => (
  <StaticQuery
    query={graphql`
      {
        allWordpressWpQuangCao(limit: 1, filter: {metadata: {position: {eq: "left"}, page: {eq: "home"}, suspend: {eq: ""}}}) {
          edges {
            node {
              id
              metadata {
                image_code
              }
            }
          }
        }
      }
    `}
    render={data => <BannerLeft data={data} {...props} />}
  />
)
