import React from "react"
import { Carousel } from "react-bootstrap"
import LazyLoad from 'react-lazyload'
import { graphql, StaticQuery } from "gatsby"

const BannerTop = ({ data }) => {
  const { allWordpressWpQuangCao = {} } = data;
  const { edges = []} = allWordpressWpQuangCao;
  const renderItem = ({ node }) => (
    <Carousel.Item key={node.id}>
      <LazyLoad>
        <div dangerouslySetInnerHTML={{ __html: node.metadata.image_code.replace("amp-img", "img")  }} />
      </LazyLoad>
    </Carousel.Item>
  )
  return (
    <Carousel controls={false} indicators={false}>
      {
        edges.map(renderItem)
      }
    </Carousel>
  );
}


export default props => (
  <StaticQuery
    query={graphql`
      {
        allWordpressWpQuangCao(limit: 1, filter: {metadata: {position: {eq: "top"}, page: {eq: "home"}, suspend: {eq: ""}}}) {
          edges {
            node {
              id
              metadata {
                image_code
              }
            }
          }
        }
      }
    `}
    render={data => <BannerTop data={data} {...props} />}
  />
)
