import React from "react"
import { Carousel } from "react-bootstrap"
import LazyLoad from 'react-lazyload'
import { graphql, StaticQuery } from "gatsby"

const BannerBottom = ({ data }) => {
  const { allWordpressWpQuangCao = {} } = data
  const { edges = [] } = allWordpressWpQuangCao
  const renderItem = ({ node }) => (
    <Carousel.Item key={node.id}
                   style={{ display: 'flex', justifyContent: 'center'}}
                   className="ads-bottom_container"
     >
      <LazyLoad>
        <div dangerouslySetInnerHTML={{ __html: node.metadata.image_code.replace("amp-img", "img") }} />
      </LazyLoad>
    </Carousel.Item>
  )
  return (
    <Carousel controls={false} indicators={false} className="ads-bottom">
        {
          edges.map(renderItem)
        }
    </Carousel>
  )
  // return (
  //   <div className="ads-bottom">
  //     <div className="ads-bottom_container">
  //       <a
  //         href="https://www.capitalindex.com/bs/vie/pages/trading-platform?utm_source=vnrebates&amp;utm_medium=banner&amp;utm_campaign=dynamic"
  //         target="blank"
  //       >
  //         <img
  //           layout="responsive"
  //           className="img-responsive"
  //           src="https://vnrebates.net/wp-content/uploads/2019/07/vnrebatesnet_50-1000_270X600.gif"
  //           alt=""
  //           title=""
  //         />
  //       </a>
  //     </div>
  //   </div>
  // )
}

export default props => (
  <StaticQuery
    query={graphql`
      {
        allWordpressWpQuangCao(limit: 1, filter: {metadata: {position: {eq: "bottom"}, page: {eq: "home"}, suspend: {eq: ""}}}) {
          edges {
            node {
              id
              metadata {
                image_code
              }
            }
          }
        }
      }
    `}
    render={data => <BannerBottom data={data} {...props} />}
  />
)
