import React from "react"
import $ from "jquery"
import { graphql, StaticQuery } from "gatsby"

class BannerRight extends React.Component {
  componentDidMount() {
    var $banner = $(".ads-right"),
      $window = $(window)
    var $wiBanner = $banner.outerWidth() * 2
    zindex($("#wrapper").outerWidth())
    $window.on("resize", function() {
      $banner
        .stop()
        .animate(
          {
            left: $(".container").width() + $(".container").offset().left + 30,
          },
          1000
        )
      zindex($("#wrapper").outerWidth())
    })
    // $banner.stop().animate( { left: $('.container').width() + $('.container').offset().left + 30 }, 1000 );
    function zindex(maxWidth) {
      if ($window.width() <= maxWidth + $wiBanner) {
        $banner.addClass("zindex")
      } else {
        $banner.removeClass("zindex")
      }
    }
  }

  render() {
    const { data } = this.props;
    const { allWordpressWpQuangCao = {} } = data;
    const { edges = []} = allWordpressWpQuangCao;
    const { node = {}} = edges[0];
    const { metadata = {} } = node;
    const { image_code = "" } = metadata;
    return (
      <div className="ads-right" dangerouslySetInnerHTML={{ __html: image_code.replace("amp-img", "img")  }}>
      </div>
    )
  }
}


export default props => (
  <StaticQuery
    query={graphql`
      {
        allWordpressWpQuangCao(limit: 1, filter: {metadata: {position: {eq: "right"}, page: {eq: "home"}, suspend: {eq: ""}}}) {
          edges {
            node {
              id
              metadata {
                image_code
              }
            }
          }
        }
      }
    `}
    render={data => <BannerRight data={data} {...props} />}
  />
)
