/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"
import "bootstrap/dist/css/bootstrap.min.css"

import Header from "./Header/Header"
import Footer from "./Footer"

import "./layout.css"
import "./layout.scss"
import LogoBannerSection from "./LogoBannerSection"
import BannerLeft from "./BannerAds/BannerLeft"
import BannerRight from "./BannerAds/BannerRight"
import BannerBottom from "./BannerAds/BannerBottom"

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <>
      <Header siteTitle={data.site.siteMetadata.title} />
      <div className="container-fluid" style={{ paddingBottom: 15 }}>
        <LogoBannerSection />
        <BannerLeft />
        {children}
        <BannerRight />
      </div>
      <BannerBottom />
      <Footer />
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
